import { Component, Input, Inject, PLATFORM_ID } from "@angular/core";
import { connectHierarchicalMenu } from "instantsearch.js/es/connectors";
import { noop, isFunction } from "lodash-es";

import { BaseWidget } from "../base-widget";
import { NgAisInstance } from "../instantsearch/instantsearch-instance";
import { bem, parseNumberInput } from "../utils";

export type HierarchicalMenuState = {
  createURL: Function;
  items: {}[];
  refine: Function;
};


export class NgAisHierarchicalMenu extends BaseWidget {
  // render option
   public transformItems?: Function;

  // connector options
   public attributes: string[];
   public separator?: string = " > ";
   public rootPath?: string;
   public showParentLevel?: boolean;
   public limit?: number | string = 10;
   public sortBy?: string[] | ((item: object) => number);

  public state: HierarchicalMenuState = {
    createURL: noop,
    items: [],
    refine: noop
  };

  get items() {
    return isFunction(this.transformItems)
      ? this.transformItems(this.state.items)
      : this.state.items;
  }

  constructor(
     public platformId: Object,
    searchInstance: NgAisInstance
  ) {
    super(searchInstance, "HierarchicalMenu");
  }

  public ngOnInit() {
    this.createWidget(connectHierarchicalMenu, {
      limit: parseNumberInput(this.limit),
      attributes: this.attributes,
      rootPath: this.rootPath,
      separator: this.separator,
      showParentLevel: this.showParentLevel,
      sortBy: this.sortBy
    });

    super.ngOnInit();
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: "ng-ais-hierarchical-menu",
  template: `
    <div [class]="cx()">
      <ng-ais-header [header]="header" [className]="cx('header')"></ng-ais-header>

      <div [class]="cx('body')">
        <ul [class]="cx('list') + ' ' + cx('list', 'lvl0')">
          <ng-ais-hierarchical-menu-item
            *ngFor="let item of items"
            [item]="item"
            [createURL]="state.createURL"
            [refine]="state.refine"
          >
          </ng-ais-hierarchical-menu-item>
        </ul>
      </div>

      <ng-ais-footer [footer]="footer" [className]="cx('footer')"></ng-ais-footer>
    </div>
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Object, decorators: [{ type: Inject, args: [PLATFORM_ID, ] }, ]},
{type: NgAisInstance, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'transformItems': [{ type: Input },],
'attributes': [{ type: Input },],
'separator': [{ type: Input },],
'rootPath': [{ type: Input },],
'showParentLevel': [{ type: Input },],
'limit': [{ type: Input },],
'sortBy': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
